@import url("https://fonts.googleapis.com/css?family=Roboto:400,400i,500,700");

body, button, html, input, select, textarea {
  font-family: Roboto, Helvetica, Arial, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

h1 small, h2 small, h3 small, h4 small, h5 small, h6 small {
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: -.02em;
  font-size: .6em;
}

h1 {
  font-size: 56px;
}

h1, h2 {
  font-weight: 400;
  line-height: 1.5;
  margin-top: 24px;
  margin-bottom: 24px;
}

h2 {
  font-size: 45px;
}

h3 {
  font-size: 34px;
  margin-bottom: 24px;
}

h3, h4 {
  font-weight: 400;
  line-height: 1.5;
  margin-top: 24px;
}

h4 {
  font-size: 24px;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: 16px;
}

h5 {
  font-size: 20px;
  font-weight: 500;
  letter-spacing: -.02em;
}

h5, h6 {
  line-height: 1.5;
  margin-top: 24px;
  margin-bottom: 16px;
}

h6 {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: .04em;
}

body, html, p {
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
}

p {
  letter-spacing: 0;
  margin-bottom: 16px;
}

blockquote {
  position: relative;
  font-size: 24px;
  font-style: italic;
  letter-spacing: .08em;
  margin-top: 24px;
  margin-bottom: 16px;
}

blockquote, ol, ul {
  font-weight: 400;
  line-height: 1.5;
}

ol, ul {
  font-size: 14px;
  letter-spacing: 0;
}

b, strong {
  font-weight: 500;
}

